import { Image, StyleSheet, Text, View } from 'react-native';

import { NativeStackScreenProps } from '@react-navigation/native-stack';
import React from 'react';
import { RootStackParamList } from '../../../App';

type DetailScreenProps = NativeStackScreenProps<RootStackParamList, 'Detalhes'>;

const DetailScreen: React.FC<DetailScreenProps> = ({ route }) => {
  const { uri, date, coords } = route.params as {
    uri: string;
    date: string;
    coords?: { latitude: number; longitude: number };
  };

  return (
    <View style={styles.container}>
      <Image source={{ uri }} style={styles.image} />
      <View style={styles.infoContainer}>
        <Text style={styles.labelText}>Informações:</Text>

        <Text style={styles.dateText}>
          <Text style={{ fontWeight: 'bold' }}>Data/hora: </Text>
          {date}
        </Text>

        {coords && (
          <Text style={styles.dateText}>
            <Text style={{ fontWeight: 'bold' }}>Coordenadas: </Text>
            {coords.latitude.toFixed(6)}, {coords.longitude.toFixed(6)}
          </Text>
        )}
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#000',
    justifyContent: 'center',
    alignItems: 'center',
  },
  image: {
    width: '100%',
    height: '100%',
    resizeMode: 'center',
  },
  infoContainer: {
    position: 'absolute',
    bottom: 20, // distância da base da tela
    left: 20, // distância da lateral
    backgroundColor: 'rgba(0,0,0,0.5)', // fundo semi-transparente
    padding: 10,
    borderRadius: 8,
  },
  labelText: {
    color: '#fff',
    fontWeight: 'bold',
    fontSize: 16,
  },
  dateText: {
    color: '#fff',
    fontSize: 14,
    marginTop: 4,
  },
});

export default DetailScreen;
